import { Component, h } from 'preact';
import { fullSize } from '../constants';
export class ChartMap extends Component {
    constructor() {
        super();
        const chartDiv = window.document.createElement('div');
        if (!chartDiv) {
            throw new Error('could not add the chart to the DOM');
        }
        this.state = { chartDiv };
    }
    componentDidMount() {
        this.base.id = 'chartMapDiv';
    }
    componentDidUpdate() {
        JSC.Chart('chartMapDiv', {
            type: 'map',
            defaultPoint: {
                tooltip: this.props.toolTip,
                z: 0,
            },
            palette: {
                pointValue: p => p.options('z'),
                ranges: this.props.ranges,
            },
            series: this.props.series,
        });
    }
    render() {
        return h("div", { className: fullSize });
    }
}
