import { h } from 'preact';
import { flex } from '../constants';
import { Button } from './button';
export function Menu({ config: { onClick, labels, selected }, }) {
    return (h("nav", { className: flex }, labels.map((label, i) => {
        return (h(Button, { isDisabled: i === selected, label: label, onClick: () => onClick(i) }));
    })));
}
