import { h } from 'preact';
import { noop } from '@ch1/utility';
export function InputString({ classes, listenKeyUp, onChange, placeholder, value, }) {
    const classString = classes ? classes.join(' ') : '';
    const change = (e) => onChange(e.target.value);
    const keyUp = listenKeyUp ? change : noop;
    return (h("input", { className: classString, onChange: change, onKeyUp: keyUp, placeholder: placeholder || '', type: "text", value: value }));
}
