import { h } from 'preact';
import { Select } from '../components/select';
import { SelectMultiple } from '../components/select-multiple';
import { InputDate } from '../components/date';
import { Button } from '../components/button';
import { flex, flexCol, flexItem20, totalString, highlight, } from '../constants';
import { SelectMultipleFilter } from './select-multiple-filter';
import { noop } from '@ch1/utility';
const dataSets = [
    'Active',
    'Confirmed',
    'Deaths',
    'Recoveries',
    'Projection: By Reverse Death',
];
const modes = ['By date', 'By first confirmed', 'By first 100 confirmed'];
const scaleTypes = ['Linear', 'Logarithmic'];
const showStates = ['Show States', 'Hide States'];
const metrics = ['By Value', 'Percent'];
export function LineGraphControls({ countries, onChange, reload, state, }) {
    function selectMode(mode) {
        onChange(Object.assign(Object.assign({}, state), { mode }));
    }
    function selectDate(ymdString) {
        onChange(Object.assign(Object.assign({}, state), { startDate: ymdString }));
    }
    function selectDataSets(dataSetIndexes) {
        onChange(Object.assign(Object.assign({}, state), { dataSetIndexes }));
    }
    function selectScaleType(scaleType) {
        onChange(Object.assign(Object.assign({}, state), { scaleType }));
    }
    function selectShowStates(showOrHide) {
        onChange(Object.assign(Object.assign({}, state), { showStates: showOrHide === 0 ? false : true }));
    }
    function selectMetric(metric) {
        onChange(Object.assign(Object.assign({}, state), { byMetric: metric }));
    }
    return (h("section", { className: `${flex} ${flexItem20}` },
        h("section", { className: flexCol },
            h(Select, { onChange: selectMode, options: modes, selected: state.mode }),
            h(Select, { onChange: selectScaleType, options: scaleTypes, selected: state.scaleType }),
            h(InputDate, { onChange: selectDate, ymdString: state.startDate })),
        h(SelectMultiple, { onChange: selectDataSets, options: dataSets, selected: state.dataSetIndexes }),
        h(SelectMultipleFilter, { classes: this.props.countryIndexes.length === 0 ? [highlight] : [], filter: this.props.state.countryFilter, onUpdateFilter: this.props.onUpdateCountryFilter, onChange: noop, onClear: this.props.clearCountries, onDeselect: this.props.selectCountry, options: countries.filter(filterStates(this.props.state.showStates)), selected: this.props.countryIndexes }),
        h("div", { className: flexCol },
            h(Select, { onChange: selectShowStates, options: showStates, selected: state.showStates ? 1 : 0 }),
            h(Select, { onChange: selectMetric, options: metrics, selected: state.byMetric }),
            h(Button, { label: "Reload", onClick: reload }))));
}
function filterStates(doFilter) {
    return (item) => {
        if (doFilter === false) {
            return true;
        }
        if (item.name.indexOf(',') > -1) {
            if (item.name.indexOf(`, ${totalString}`) > -1) {
                return true;
            }
            return false;
        }
        return true;
    };
}
