import { Component, h } from 'preact';
import { InputString } from './string';
import { SelectMultiple } from './select-multiple';
import { flexCol, flex } from '../constants';
import { isString } from '@ch1/utility';
import { Button } from './button';
import { isMobile } from '../utility';
export class SelectMultipleFilter extends Component {
    constructor() {
        super();
    }
    getOptions() {
        if (this.props.filter) {
            return this.props.options.filter(option => {
                if (isString(option)) {
                    if (option.toLowerCase().indexOf(this.props.filter.toLowerCase()) > -1) {
                        return true;
                    }
                    return false;
                }
                else {
                    if (option.name.toLowerCase().indexOf(this.props.filter.toLowerCase()) >
                        -1) {
                        return true;
                    }
                    return false;
                }
            });
        }
        else {
            return this.props.options;
        }
    }
    render() {
        const givenClasses = this.props.classes ? this.props.classes.join(' ') : '';
        const className = givenClasses.length
            ? `${givenClasses} ${flexCol}`
            : flexCol;
        const onChange = (selected) => {
            this.props.onChange(selected);
        };
        const onDeselect = (index) => {
            this.props.onDeselect(index);
        };
        return (h("div", { className: className },
            h("div", { className: flex },
                isMobile() ? ('') : (h(InputString, { listenKeyUp: true, onChange: this.props.onUpdateFilter.bind(this), placeholder: "filter", value: this.props.filter })),
                h(Button, { label: "\u2717", onClick: this.props.onClear })),
            isMobile() ? (h(SelectMultiple, { onChange: onChange, options: this.getOptions(), selected: this.props.selected })) : (h(SelectMultiple, { onChange: onChange, onClick: onDeselect, options: this.getOptions(), selected: this.props.selected }))));
    }
}
